'use client'

import { useStore } from '@/store/useStore'
import { useRouter } from 'next/navigation'

export default function CouponsPage() {
    const { coupons, useCoupon } = useStore()
    const router = useRouter()

    const regularCoupons = coupons.filter((coupon) => coupon.type === 'regular')
    const freeCoupons = coupons.filter((coupon) => coupon.type === 'free')

    return (
        <div className="bg-white">
            <div className="mx-auto max-w-2xl px-4 py-16 sm:px-6 sm:py-24 lg:max-w-7xl lg:px-8">
                <h2 className="text-2xl font-bold tracking-tight text-gray-900">내 쿠폰</h2>

                <div className="mt-8 grid grid-cols-1 gap-6 sm:grid-cols-2">
                    <div className="rounded-lg border border-gray-200 p-6">
                        <h3 className="text-lg font-medium text-gray-900">일반 쿠폰</h3>
                        <p className="mt-2 text-3xl font-bold text-indigo-600">
                            {regularCoupons.length}장
                        </p>
                        <p className="mt-2 text-sm text-gray-500">
                            {regularCoupons.length >= 10
                                ? '10장이 모여 무료 음료 쿠폰으로 자동 교환되었습니다!'
                                : `${10 - regularCoupons.length}장 더 모으면 무료 음료 쿠폰으로 교환됩니다.`}
                        </p>
                    </div>

                    <div className="rounded-lg border border-gray-200 p-6">
                        <h3 className="text-lg font-medium text-gray-900">무료 음료 쿠폰</h3>
                        <p className="mt-2 text-3xl font-bold text-indigo-600">
                            {freeCoupons.length}장
                        </p>
                        {freeCoupons.length > 0 && (
                            <button
                                onClick={() => {
                                    useCoupon(freeCoupons[0].id)
                                    router.push('/menu')
                                }}
                                className="mt-4 w-full rounded-md bg-indigo-600 px-3.5 py-2.5 text-sm font-semibold text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
                            >
                                쿠폰 사용하기
                            </button>
                        )}
                    </div>
                </div>

                <div className="mt-8">
                    <h3 className="text-lg font-medium text-gray-900">쿠폰 적립 내역</h3>
                    <div className="mt-4 flow-root">
                        <div className="-mx-4 -my-2 overflow-x-auto sm:-mx-6 lg:-mx-8">
                            <div className="inline-block min-w-full py-2 align-middle sm:px-6 lg:px-8">
                                <table className="min-w-full divide-y divide-gray-300">
                                    <thead>
                                        <tr>
                                            <th
                                                scope="col"
                                                className="py-3.5 pl-4 pr-3 text-left text-sm font-semibold text-gray-900 sm:pl-0"
                                            >
                                                쿠폰 종류
                                            </th>
                                            <th
                                                scope="col"
                                                className="px-3 py-3.5 text-left text-sm font-semibold text-gray-900"
                                            >
                                                적립일
                                            </th>
                                        </tr>
                                    </thead>
                                    <tbody className="divide-y divide-gray-200">
                                        {coupons.map((coupon) => (
                                            <tr key={coupon.id}>
                                                <td className="whitespace-nowrap py-4 pl-4 pr-3 text-sm font-medium text-gray-900 sm:pl-0">
                                                    {coupon.type === 'regular' ? '일반 쿠폰' : '무료 음료 쿠폰'}
                                                </td>
                                                <td className="whitespace-nowrap px-3 py-4 text-sm text-gray-500">
                                                    {new Date(coupon.createdAt).toLocaleString()}
                                                </td>
                                            </tr>
                                        ))}
                                    </tbody>
                                </table>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
} 